.subscription {
	display: flex;
	flex-direction: column;
	padding: 20px 15px;
	background-color: $color-white;

	@include desktop {
		padding: 20px;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
	}

	&__text-box {
		margin-bottom: 40px;

		@include desktop {
			margin-bottom: 0;
		}
	}

	&__title {
		margin-bottom: 5px;
		font-size: 21px;
		line-height: 26px;
		font-weight: 600;
	}

	&__text {
		font-size: 16px;
		line-height: 26px;
	}

	&__button {
		flex-shrink: 0;
		width: 100%;

		@include desktop {
			max-width: 200px;
			margin-left: 30px;
		}
	}
}
