.dropdown {
  position: relative;
  color: #18181B; }
  .dropdown--active {
    z-index: 1; }
  .dropdown__value-box {
    position: relative;
    border: 1px solid #D4D4D8;
    border-radius: 3px;
    font-size: 16px;
    line-height: 26px;
    color: #3F3F46;
    overflow: hidden;
    cursor: pointer;
    transition: border-color 0.2s ease; }
    .dropdown--active:not(.dropdown--navigate) .dropdown__value-box {
      border-color: #FF6A00;
      border-radius: 3px 3px 0 0; }
  .dropdown__value {
    width: 100%;
