.section-hero {
	position: relative;
	z-index: 0;
	display: flex;
	flex-direction: column;
	margin-bottom: 36px;
	padding-top: 36px;
	padding-bottom: 14px;

	@include desktop {
		margin-bottom: 80px;
		padding-bottom: 14px;
		min-height: calc(100vh - 78px);
	}

	&__container {
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		&::before {
			content:"";
		}
	}

	&__bg-box {
		position: relative;
		order: 1;

		&::before {
			display: block;
			padding-top: 50%;
			content: "";
		}

		@include desktop {
			position: absolute;
			z-index: -1;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			&::before {
				position: absolute;
				z-index: 1;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				padding-top: 0;
				background: linear-gradient(224deg, rgba(232, 232, 232, 0.40) 60.86%, rgba(255, 255, 255, 0.00) 100%);
			}
		}
	}

	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
		transform: scaleX(-1);

		@include desktop {
			position: relative;
			transform: none;
		}
	}

	&__content-box {
		max-width: 636px;
	}

	&__title {
		margin-bottom: 30px;
		font-size: 38px;
		line-height: 1.3;
		font-weight: 700;
		text-transform: uppercase;

		@include desktop {
			margin-bottom: 27px;
			font-size: 81px;
		}
	}

	&__text {
		margin-bottom: 30px;
		font-size: 16px;
		line-height: 26px;
		font-weight: 600;

		@include desktop {
			font-size: 19px;
			line-height: 31px;
		}
	}

	&__button {
		width: 100%;
		margin-bottom: 31px;

		@include desktop {
			max-width: 250px;
			margin-bottom: 136px;
		}
	}

	&__bottom-row {
		display: flex;
	}

	&__list {
		display: flex;
		flex-direction: column;
		max-width: 970px;
		margin-bottom: 10px;

		@include desktop {
			flex-direction: row;
			justify-content: space-between;
			margin: 0 -18px;
		}
	}

	&__list-elem {
		position: relative;
		margin-bottom: 20px;
		padding-left: 35px;
		font-size: 16px;
		line-height: 26px;
		font-weight: 600;
		color: #9CA3AF;

		@include desktop {
			margin: 0 18px;
			padding-left: 41px;
			font-size: 19px;
			line-height: 31px;
			color: inherit;
		}
	}

	&__list-marker {
		position: absolute;
		top: 0;
		left: 0;
		width: 24px;
		height: 24px;

		@include desktop {
			width: 29px;
			height: 29px;
		}
	}

	&__arrow {
		display: none;
		align-items: center;
		justify-content: center;
		width: 48px;
		height: 48px;
		margin-right: 75px;
		margin-left: -7px;
		animation: yoyo ease-in-out .7s infinite alternate;

		@include desktop {
			display: flex;
		}
	}

	&__arrow-icon {
		width: 24px;
		height: 24px;
	}
}

@keyframes yoyo {
	0% {
		transform: translateY(0);
	}
	100% {
		transform: translateY(14px);
	}
}
