*,
*:before,
*:after {
	box-sizing: border-box;
}

html {
	overflow-x: hidden;

	&.menu-show {
		overflow: hidden;

		@include desktop {
			overflow-y: auto;
		}
	}
}

body {
	position: relative;
	display: flex;
	flex-direction: column;
	width: 100vw;
	min-width: 375px;
	min-height: 100vh;
	padding-top: 62px;
	overflow-x: hidden;
	-webkit-font-smoothing: antialiased;
	color: $color-black-text;
	font-size: 16px;
	line-height: 1.3;

	@include desktop {
		padding-top: 78px;
	}

	.menu-show & {
		overflow: hidden;

		@include desktop {
			overflow-y: auto;
		}
	}
}

.main {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	overflow: hidden;
}

button {
	border: none;
	background-color: $color-transparent;
	cursor: pointer;
}

svg {
	fill: none;
}

a {
	color: inherit;
	text-decoration: none;
}
