.order-detail {
	@include desktop {
		display: grid;
		grid-template-columns: 1fr 1.66fr;
	}

	&__description-box {
		margin-bottom: 40px;
		padding: 20px;
		background-color: $color-white;

		@include desktop {
			margin-bottom: 0;
		}
	}

	&__title {
		margin-bottom: 20px;
		font-size: 21px;
		line-height: 26px;
		font-weight: 600;
	}

	&__text {
		font-size: 16px;
		line-height: 26px;
		color: #3F3F46;

		&:not(:last-child) {
			margin-bottom: 10px;
		}
	}

	&__data-box {
		padding: 20px;
	}

	&__subtitle {
		font-size: 16px;
		line-height: 26px;
		font-weight: 600;
	}

	&__data {
		margin-bottom: 30px;
	}

	&__data-row {
		display: flex;
		justify-content: space-between;
		padding: 10px 0;

		&:not(:last-child) {
			border-bottom: 1px solid #D9D9D9;
		}
	}

	&__data-text {
		font-size: 16px;
		line-height: 26px;
	}

	&__note {
		font-size: 16px;
		line-height: 26px;
		color: $color-gray500;
	}
}
