.option {
	display: block;
	font-size: 16px;
	line-height: 22px;
	color: $color-gray500;

	&__input {
		display: none;
	}

	&__text {
		position: relative;
		display: flex;
		padding: 13px 12px 13px 40px;
		background-color: $color-white;
		border: 1px solid #D4D4D8;
		border-radius: 3px;
		user-select: none;
		cursor: pointer;

		&::before {
			position: absolute;
			top: 50%;
			left: 12px;
			transform: translateY(-50%);
			width: 1rem;
			height: 1rem;
			border: 1px solid $color-gray300;
			border-radius: 50%;
			content: "";
			transition: border-color $transition-default;
		}

		&::after {
			position: absolute;
			top: 50%;
			left: 16px;
			transform: translateY(-50%);
			width: .5rem;
			height: .5rem;
			border: 1px solid $color-gray300;
			border-radius: 50%;
			content: "";
			transition: border-color $transition-default, background-color $transition-default;
		}

		&:hover {
			&::before, &::after {
				border-color: rgba($color-purple800, .5);
			}
		}

		.option--checkbox & {
			padding: 14px 12px 14px 50px;

			&::before {
				width: 24px;
				height: 24px;
				border: 2px solid $color-gray300;
				border-radius: 4px;
			}

			&::after {
				left: 17px;
				width: 14px;
				height: 14px;
				border-radius: 2px;
				border: none;
				background-image: url("data:image/svg+xml,%3Csvg width='11' height='8' viewBox='0 0 11 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.999911 3.59823L4.27767 6.87599L9.8759 1.27776' stroke='white'/%3E%3C/svg%3E");
				background-size: 10px 10px;
				background-position: center;
				background-repeat: no-repeat;
			}

			&:hover {
				&::before {
					border-color: rgba($color-purple800, .5);
				}

				&::after {
					background-color: rgba($color-purple800, .5);
				}
			}
		}

		.option__input:checked + & {
			&::before {
				border-color: $color-purple800;
			}

			&::after {
				border-color: $color-purple800;
				background-color: $color-purple800;
			}
		}
	}
}
