.footer {
	padding-top: 80px;

	&__content {
		@include desktop {
			display: flex;
			padding-bottom: 73px;
		}
	}

	&__logo {
		display: inline-block;
		margin-bottom: 21px;
		font-size: 21px;
		line-height: 24px;
		font-weight: 700;
		text-transform: uppercase;

		@include desktop {
			max-width: 100px;
			margin-right: 74px;
			margin-bottom: 0;
		}
	}

	&__text {
		max-width: 500px;
		margin-bottom: 40px;
		font-size: 16px;
		line-height: 26px;

		@include desktop {
			margin-right: auto;
			margin-bottom: 0;
		}
	}

	&__nav-box {
		margin-bottom: 18px;
		@include desktop {
			display: flex;
			margin-right: 24px;
			margin-bottom: 0;
		}
	}

	&__nav {
		@include desktop {
			padding: 0 20px;
		}
	}

	&__nav-elem {
		margin-bottom: 5px;
	}

	&__nav-link {
		font-size: 16px;
		line-height: 26px;
		color: $color-gray400;
		transition: color $transition-default;

		&:hover {
			color: $color-orange;
		}
	}

	&__copyright {
		display: flex;
		flex-direction: column-reverse;
		padding-top: 20px;
		padding-bottom: 20px;
		border-top: 1px solid currentColor;
		color: rgba(#000, 0.3);
		font-size: 14px;
		line-height: 26px;

		@include desktop {
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
			padding-top: 20px;
			padding-bottom: 20px;
		}
	}

	&__copyright-text {
		letter-spacing: -0.04em;
		text-align: center;

		@include desktop {
			letter-spacing: unset;
			text-align: left;
		}
	}

	&__copyright-nav {
		display: flex;
		justify-content: space-between;
		margin-bottom: 9px;

		@include desktop {
			margin: 0 -20px;
		}
	}

	&__copyright-nav-elem {
		@include desktop {
			padding: 0 20px;
		}
	}

	&__copyright-nav-link {
		transition: color $transition-default;

		&:hover {
			color: $color-orange;
		}
	}
}
