.dropdown {
	position: relative;
	color: $color-black;

	&--active {
		z-index: 1;
	}

	&__value-box {
		position: relative;
		border: 1px solid #D4D4D8;
		border-radius: 3px;
		font-size: 16px;
		line-height: 26px;
		color: #3F3F46;
		overflow: hidden;
		cursor: pointer;
		transition: border-color $transition-default;

		.dropdown--active:not(.dropdown--navigate) & {
			border-color: $color-orange;
			border-radius: 3px 3px 0 0;
		}
	}

	&__value {
		width: 100%;
		padding: 11px 37px 11px 12px;
		border: none;
		background-color: $color-white;
		pointer-events: none;
		text-overflow: ellipsis;

		&::placeholder {
			font-size: 16px;
			line-height: 26px;
			color: $color-gray500;
			opacity: 1;
		}

		&:focus {
			outline: none;
		}

		.dropdown--navigate & {
			color: $color-orange;
		}
	}

	&__options-list {
		position: absolute;
		z-index: -1;
		top: calc(100% - 1px);
		left: 0;
		min-width: 100%;
		margin: 0;
		padding: 15px 0;
		border: 1px solid #D4D4D8;
		border-radius: 0 0 3px 3px;
		background-color: $color-white;
		transform: scaleY(0);
		transform-origin: 50% 0;
		transition: transform $transition-default;

		.dropdown--active & {
			transform: scaleY(1);
		}
	}

	&__option {
		position: relative;
		padding: 6px 14px;
		transition: background-color $transition-default;
		cursor: pointer;

		&:hover {
			background-color:$color-gray-light;
		}

		&--selected {
			display: none;
		}
	}

	&__arrow {
		position: absolute;
		top: 50%;
		right: 12px;
		transform: translateY(-50%);
		width: 16px;
		height: 16px;
		transition: transform $transition-default;

		.dropdown--active & {
			transform: translateY(-50%) rotate(-180deg);
		}
	}
}
