.no-orders {
	padding: 65px 20px;
	text-align: center;
	background-color: $color-white;

	&__title {
		font-size: 21px;
		line-height: 26px;
		font-weight: 600;
	}

	&__text {
		font-size: 16px;
		line-height: 26px;
		color: $color-gray500;
	}

	&__link {
		color: $color-orange;

		&:hover {
			text-decoration: underline;
		}
	}
}
