.section {
	position: relative;
	padding-top: 50px;
	padding-bottom: 50px;
	overflow-x: hidden;

	@include desktop {
		padding-top: 80px;
		padding-bottom: 80px;
	}

	&--with-right-text {
		display: flex;
		justify-content: space-between;
		user-select: none;
	}

	&__content-box {
		.section--how-it-works &,
		.section--advantages & {
			width: calc(100% - 46px);
		}

		@include desktop {
			flex-grow: 1;
			max-width: 1024px;
			width: unset;
		}
	}

	&--gray {
		margin-top: 30px;
		margin-bottom: 30px;
		background-color: $color-gray-light;

		@include desktop {
			margin-top: 80px;
			margin-bottom: 80px;
		}
	}

	&__title-with-link {
		display: flex;

		@include desktop {
			display: block;
		}
	}

	&__title {
		margin-bottom: 40px;
		font-size: 38px;
		line-height: 46px;
		font-weight: 700;

		.section--ready & {
			margin-bottom: 34px;
		}
	}

	&__text {
		margin-bottom: 30px;
		font-size: 16px;
		line-height: 26px;

		.section--services & {
			&:last-child {
				margin-bottom: 40px;
			}

			@include desktop {
				margin-bottom: 0;
				&:last-child {
					margin-bottom: 40px;
				}
			}
		}

		.section--plan & {
			margin-bottom: 26px;
		}
	}

	&__button {
		width: 100%;

		@include desktop {
			max-width: 250px;
		}

		.section--plan & {
			margin-top: 15px;
		}

		.section--ready & {
			margin-top: 10px;
		}
	}

	&__right-text {
		margin-right: 20px;
		font-size: 16px;
		line-height: 26px;
		writing-mode: vertical-rl;
		text-align: right;
		transform: rotate(180deg);

		@include desktop {
			font-size: 21px;
		}

		&--white-bg {
			position: relative;
			z-index: 5;
			background-color: $color-white;

			&::after {
				position: absolute;
				z-index: -1;
				top: 1px;
				left: 0;
				width: 56px;
				height: 100%;
				background: $color-white;
				content: "";
			}
		}
	}

	&__grid {
		display: grid;
		grid-gap: 30px;

		.section--how-it-works & {
			display: flex;
			grid-gap: unset;

			@include desktop {
				display: grid;
				grid-template: auto / repeat(3, 1fr);
				grid-gap: 30px;
			}
		}

		.section--plan & {
			display: flex;
			flex-direction: column-reverse;
			padding-top: 40px;
			padding-bottom: 36px;

			@include desktop {
				display: grid;
				grid-template: auto / 1.36fr 1fr;
				padding-top: 40px;
				padding-bottom: 36px;
			}
		}

		.section--advantages & {
			display: flex;
			grid-gap: unset;

			@include desktop {
				display: grid;
				grid-template: auto / repeat(2, 1fr);
				grid-gap: 30px;
			}
		}

		.section--ready & {
			display: flex;
			flex-direction: column-reverse;

			@include desktop {
				display: grid;
				grid-template: auto / minmax(200px, 576px) minmax(200px, 497px);
				justify-content: space-between;
				padding-top: 40px;
				padding-bottom: 36px;
			}
		}
	}

	&__col {
		&:nth-child(1) {
			.section--plan &,
			.section--ready & {
				display: flex;
				flex-direction: column;
			}
		}
	}

	&__text-box {
		.section--plan & {
			@include desktop {
				max-width: 500px;
				margin-left: auto;
			}
		}

		.section--ready & {
			@include desktop {
				max-width: 410px;
				margin-left: auto;
			}
		}
	}

	&__list-title {
		margin-bottom: 25px;
		font-size: 16px;
		line-height: 26px;
		font-weight: 500;
		text-transform: uppercase;
	}

	&__list {
		margin-bottom: 30px;

		.section--services & {
			margin-bottom: 40px;
		}

		.section--ready & {
			@include desktop {
				display: grid;
				grid-template: auto / repeat(2, auto);
				grid-gap: 24px 40px;
			}
		}
	}

	&__list-elem {
		font-size: 16px;
		line-height: 26px;
		font-weight: 600;

		.section--services & {
			margin-bottom: 19px;
		}

		.section--ready & {
			margin-bottom: 24px;
			@include desktop {
				margin-bottom: 0;
			}
		}
	}

	&__list-marker {
		width: 24px;
		height: 24px;
		margin-right: 10px;
		transform: translateY(4px);
	}

	&__slider {
		margin: 0;
		overflow: visible;
	}
}
