.header {
	position: absolute;
	z-index: 100;
	top: 0;
	left: 0;
	width: 100vw;
	min-width: 375px;
	padding-top: 10px;
	padding-bottom: 10px;
	background-color: $color-white;
	border-top: 1px solid $color-transparent;
	color: $color-black-header;
	transition: background-color $transition-default, transform $transition-default;

	&--fixed {
		position: fixed;
	}

	.menu-show & {
		background-color: #FAF9FA;

		@include desktop {
			background-color: $color-white;
		}
	}

	@include desktop {
		padding-top: 25px;
		padding-bottom: 25px;
	}

	&__container {
		display: flex;
		align-items: center;
		justify-content: space-between;

		@include desktop {
			justify-content: unset;
		}
	}

	&__logo {
		font-size: 21px;
		font-weight: 700;
		text-transform: uppercase;
		white-space: nowrap;

		@include desktop {
			margin-right: 230px;
		}
	}

	&__nav {
		flex-grow: 1;
		position: absolute;
		top: calc(100% - 1px);
		left: 0;
		right: 0;
		display: none;
		flex-direction: column;
		width: 100%;
		overflow-y: auto;
		background-color: #FAF9FA;
		border-top: 1px solid #E4E4E7;
		border-bottom: 1px solid #E4E4E7;
		transform: translateX(100%);
		opacity: 0;
		transition: transform $transition-default, opacity $transition-default;

		@media (max-height: 500px) {
			height: calc(100vh - 61px);
		}

		@include desktop {
			position: static;
			z-index: 0;
			display: flex;
			justify-content: space-between;
			align-items: center;
			flex-direction: row;
			width: auto;
			height: auto;
			overflow-y: unset;
			border-top: none;
			border-bottom: none;
			transform: none;
			background-color: $color-transparent;
			opacity: 1;
		}

		.menu-show & {
			display: flex;
		}

		.menu-shown & {
			transform: none;
			opacity: 1;
		}
	}

	&__nav-list {
		display: flex;
		justify-content: center;
		flex-direction: column;
		width: 100%;

		@include desktop {
			flex-grow: 1;
			flex-direction: row;
			width: auto;
			margin: 0 -20px;
		}
	}

	&__nav-elem {
		border-bottom: 1px solid #E4E4E7;

		@include desktop {
			border-bottom: none;
		}
	}

	&__nav-link {
		display: inline-block;
		width: 100%;
		padding: 20px;
		font-size: 16px;
		line-height: 26px;
		font-weight: 600;
		text-align: center;
		transition: color $transition-default;

		&--active,
		&:hover {
			color: $color-orange;
		}

		@include desktop {
			width: auto;
			padding: 0 20px;
		}
	}

	&__login {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		padding: 20px;
		font-size: 16px;
		line-height: 26px;
		font-weight: 600;
		transition: color $transition-default;

		&:hover {
			color: $color-orange;
		}

		@include desktop {
			width: auto;
			padding: 0;
		}
	}

	&__login-icon {
		width: 24px;
		height: 24px;
		margin-right: 10px;
	}

	&__menu-button {
		position: relative;
		width: 42px;
		height: 42px;
		transform: translateX(9px);

		@include desktop {
			display: none;
		}
	}

	&__menu-icon {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 24px;
		height: 24px;
		transition: opacity $transition-default;

		&--burger {
			.menu-show & {
				opacity: 0;
			}
		}

		&--close {
			opacity: 0;

			.menu-show & {
				opacity: 1;
			}
		}
	}
}
