.info-panel {
	display: block;
	padding: 20px 20px 0;
	background-color: $color-white;

	&__title {
		margin-bottom: 20px;
		font-size: 12px;
		color: $color-orange;
	}

	&__row {
		@include desktop {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			grid-column-gap: 10px;
		}
	}

	&__group {
		margin-bottom: 20px;
	}
}
