.form-group {
	position: relative;

	&__input {
		min-width: 0;
		width: 100%;
		background-color: $color-white;
		padding: 11px 12px;
		border: 1px solid #D4D4D8;
		border-radius: 3px;
		font-size: 16px;
		line-height: 26px;
		color: #3F3F46;

		&::placeholder {
			font-size: 16px;
			line-height: 26px;
			color: $color-gray500;
			opacity: 1;
		}

		&:focus {
			border-color: $color-orange;
		}

		&--date {
			padding-right: 36px;
			background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19.2 4.875H17.55V3.75C17.55 3.375 17.25 3 16.8 3C16.425 3 16.05 3.3 16.05 3.75V4.875H7.875V3.75C7.875 3.375 7.575 3 7.125 3C6.75 3 6.375 3.3 6.375 3.75V4.875H4.8C3.825 4.875 3 5.625 3 6.675V19.2C3 20.175 3.825 21 4.8 21H19.2C20.175 21 21 20.175 21 19.2V6.675C21 5.625 20.175 4.875 19.2 4.875ZM7.2 7.2C7.575 7.2 7.95 6.9 7.95 6.45V6.3H16.125V6.45C16.125 6.825 16.425 7.2 16.875 7.2C17.25 7.2 17.625 6.9 17.625 6.45V6.3H19.2C19.425 6.3 19.65 6.525 19.65 6.75V8.475H4.425V6.75C4.425 6.525 4.65 6.3 4.875 6.3H6.45V6.45C6.45 6.825 6.75 7.2 7.2 7.2ZM19.575 19.125C19.575 19.35 19.35 19.575 19.125 19.575H4.875C4.65 19.575 4.425 19.35 4.425 19.125V13.65V9.975H19.575V19.125Z' fill='%23111827'/%3E%3C/svg%3E");
			background-size: 24px 24px;
			background-position: calc(100% - 12px) center;
			background-repeat: no-repeat;
		}
	}

	&__label {
		display: block;
		margin-bottom: 9px;
		font-size: 14px;
		line-height: 1;
		color: $color-gray500;

		.form-group--small-label & {
			position: absolute;
			z-index: 1;
			top: 0;
			left: 12px;
			transform: translateY(-50%);
			display: inline-block;
			background-color: $color-white;
			font-size: 12px;
			pointer-events: none;
		}
	}
}
