.survey {
	background-color: $color-gray-medium;

	@include desktop {
		background-color: $color-white;
	}

	&__container {
		flex-grow: 1;
		display: flex;
		flex-direction: column;

		@include desktop {
			padding-bottom: 76px;
		}
	}

	&__box {
		flex-grow: 1;
		padding-top: 60px;
		padding-bottom: 60px;
		background-color: $color-gray-medium;
	}

	&__content {
		max-width: 357px;
		margin: 0 auto;
	}

	&__title {
		margin-bottom: 40px;
		font-size: 21px;
		line-height: 26px;
		font-weight: 600;
		text-align: center;
	}

	&__row {
		display: grid;
		grid-template: auto / repeat(2, 1fr);
		grid-column-gap: 10px;
	}

	&__group {
		margin-bottom: 20px;

		&--with-label {
			margin-bottom: 16px;
		}
	}

	&__button {
		width: 100%;
		margin-top: 10px;
		margin-bottom: 10px;
	}

	&__button-row {
		display: grid;
		grid-template: auto / repeat(2, 1fr);
		grid-column-gap: 10px;

		&--margin-top {
			margin-top: 30px;
		}
	}

	&__description {
		font-size: 12px;
		line-height: 1.5;
		letter-spacing: -.025em;
		color: #9CA3AF;
		text-align: center;
	}

	&__description-link {
		transition: color $transition-default;

		&:hover {
			color: $color-orange;
		}
	}

	&__option {
		margin-bottom: 15px;
	}

	&__card-row {
		display: grid;
		grid-template: auto / 100%;
		grid-gap: 20px;
		width: 100%;
		max-width: 460px;
		margin: 0 auto;

		@include tablet-desktop {
			grid-template: auto / repeat(2, 1fr);
		}
	}

	&__card {
		padding: 20px;
		background-color: #071D82;
		color: $color-white;
	}

	&__card-text {
		font-size: 16px;
		line-height: 26px;
	}

	&__card-title {
		margin-bottom: 65px;
		font-size: 21px;
		line-height: 26px;
		font-weight: 600;
	}

	&__card-link {
		width: 100%;
		text-transform: uppercase;
	}
}
