.profile {
	background-color: $color-gray-medium;

	@include desktop {
		background-color: $color-white;
	}

	&__container {
		flex-grow: 1;
		display: flex;
		flex-direction: column;

		@include desktop {
			padding-bottom: 76px;
		}
	}

	&__box {
		flex-grow: 1;
		padding: 40px 0;
		background-color: $color-gray-medium;

		@include desktop {
			padding: 40px;
		}
	}

	&__menu{
		background-color: $color-white;
		border-top: 1px solid #F4F4F5;
	}
	&__menu-list{
		display: none;
		justify-content: center;
		padding: 20px 0;

		@include tablet-desktop {
			display: flex;
		}
	}
	&__menu-elem{
		margin: 0 20px;
	}
	&__menu-link{
		font-size: 12px;
		line-height: 16px;
		text-transform: uppercase;

		&--active,
		&:hover{
			color: $color-orange;
		}
	}

	&__mobile-menu {
		padding-top: 15px;
		padding-bottom: 15px;
		border-top: 1px solid #F4F4F5;
		@include tablet-desktop {
			display: none;
		}
	}

	&__no-orders {
		width: 100%;
		max-width: 700px;
		margin: 0 auto;
	}

	&__subscription-list {
		display: grid;
		grid-row-gap: 20px;
		max-width: 700px;
		margin: 0 auto;
	}

	&__user-info {
		display: grid;
		grid-row-gap: 20px;
		max-width: 700px;
		margin: 0 auto;
	}

	&__user-info-button {
		width: 100%;
	}
}
