.button {
	display: inline-block;
	padding: 15px 30px;
	font-size: 16px;
	font-weight: 700;
	line-height: 1;
	white-space: nowrap;
	text-align: center;
	border-width: 1px;
	border-style: solid;
	transition: background-color $transition-default,border-color $transition-default, color $transition-default;

	&--lg {
		padding: 19px 36px;
		font-size: 19px;
	}

	&--orange {
		background-color: $color-orange;
		border-color: $color-orange;
		color: $color-white;

		&:hover {
			background-color: darken($color-orange, 5%);
			border-color: darken($color-orange, 5%);
		}

		&:disabled {
			border-color: $color-gray300;
			background-color: $color-gray300;
			color: $color-white;
			cursor: not-allowed;
		}
	}

	&--outline-gray {
		background-color: $color-transparent;
		border-color: #D4D4D8;
		color: $color-black-text;

		&:hover {
			background-color: #D4D4D8;
		}
	}

	&--outline-white {
		background-color: $color-transparent;
		border-color: $color-white;
		font-size: 12px;
		font-weight: 400;
		color:  $color-white;

		&:hover {
			border-color: $color-orange;
			color: $color-orange;
		}
	}

	&--uppercase {
		font-size: 12px;
		font-weight: 400;
		text-transform: uppercase;
	}
}
